.title-interior {
  margin-top: 140px;
  margin-bottom: 27px;
}
@media (max-width: 1023px) {
  .title-interior {
    margin-top: 50px;
  }
}
@media (max-width: 639px) {
  .title-interior {
    margin-top: 30px;
  }
}

.desc-interior {
  max-width: 930px;
  font-size: 16px;
  margin-bottom: 60px;
}
@media (max-width: 1279px) {
  .desc-interior {
    margin-bottom: 87px;
  }
}
@media (max-width: 639px) {
  .desc-interior {
    margin-bottom: 85px;
  }
}
.desc-interior ul {
  padding-left: 0;
}
.desc-interior li {
  list-style-type: none;
  position: relative;
  padding-left: 15px;
  margin-bottom: 5px;
}
.desc-interior li::before {
  content: "-";
  position: absolute;
  left: 0;
  top: 1px;
}

.slider-interior {
  position: relative;
}

.swiper-pagination {
  display: flex;
  right: 0;
  bottom: calc(100% + 90px);
}
@media (max-width: 1279px) {
  .swiper-pagination {
    bottom: calc(100% + 20px);
  }
}

.swiper-button-prev,
.swiper-button-next {
  width: 56px;
  height: 56px;
  position: relative;
  border: none;
  background-color: #000000;
  color: #ffffff;
}
.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 26px;
}

.swiper-button-prev {
  left: 0;
  margin-right: 10px;
}

.swiper-button-next {
  right: 0;
}

.single-news {
  padding-top: 80px;
  padding-bottom: 140px;
}
@media (max-width: 1023px) {
  .single-news {
    padding-top: 30px;
    padding-bottom: 70px;
  }
}
@media (max-width: 639px) {
  .single-news {
    padding-bottom: 40px;
  }
}

.single-news-title {
  font-size: 40px;
  line-height: 130%;
  font-weight: 600;
  margin-bottom: 24px;
}
@media (max-width: 1023px) {
  .single-news-title {
    font-size: 35px;
  }
}
@media (max-width: 639px) {
  .single-news-title {
    font-size: 22px;
    margin-bottom: 20px;
  }
}

.single-news-date {
  font-size: 16px;
  color: #000000;
  font-weight: 300;
  line-height: 24px;
  margin-bottom: 60px;
}
@media (max-width: 1023px) {
  .single-news-date {
    margin-bottom: 40px;
  }
}
@media (max-width: 639px) {
  .single-news-date {
    margin-bottom: 30px;
  }
}

.single-news-thumb {
  margin-bottom: 48px;
}
@media (max-width: 1023px) {
  .single-news-thumb {
    margin-bottom: 30px;
  }
}
@media (max-width: 639px) {
  .single-news-thumb {
    margin-bottom: 25px;
  }
}

.single-news-content {
  font-size: 20px;
  line-height: 150%;
}
@media (max-width: 1023px) {
  .single-news-content {
    font-size: 16px;
  }
}
.single-news-content h2 {
  font-size: 32px;
  line-height: 130%;
  font-weight: 600;
  margin-bottom: 24px;
}
@media (max-width: 1023px) {
  .single-news-content h2 {
    font-size: 26px;
  }
}
@media (max-width: 639px) {
  .single-news-content h2 {
    font-size: 18px;
    margin-bottom: 20px;
  }
}
.single-news-content h3 {
  font-size: 25px;
  line-height: 130%;
  font-weight: 600;
  margin-bottom: 18px;
}
@media (max-width: 1023px) {
  .single-news-content h3 {
    font-size: 16px;
  }
}
@media (max-width: 639px) {
  .single-news-content h3 {
    font-size: 22px;
    margin-bottom: 20px;
  }
}
.single-news-content h4 {
  font-size: 18px;
  line-height: 130%;
  font-weight: 600;
  margin-bottom: 18px;
}
.single-news-content blockquote {
  background-color: #F6F6F7;
  border-radius: 12px;
  padding: 32px;
  color: #181A2A;
  font-style: italic;
  font-size: 24px;
  line-height: 32px;
  margin-top: 48px;
  margin-bottom: 48px;
  margin-left: 0;
  margin-right: 0;
}
.single-news-content a {
  text-decoration: underline;
  color: #3FA9F5;
}

.page-news {
  padding-top: 80px;
  padding-bottom: 140px;
}
@media (max-width: 1023px) {
  .page-news {
    padding-top: 30px;
    padding-bottom: 70px;
  }
}
@media (max-width: 639px) {
  .page-news {
    padding-bottom: 40px;
  }
}
.page-news h1 {
  font-weight: 600;
  margin-top: 70px;
  line-height: 100%;
  margin-bottom: 70px;
  font-size: 48px;
}
@media (max-width: 1023px) {
  .page-news h1 {
    font-size: 35px;
    margin-top: 40px;
    margin-bottom: 40px;
  }
}
@media (max-width: 639px) {
  .page-news h1 {
    font-size: 22px;
    margin-top: 30px;
    margin-bottom: 30px;
  }
}

.news__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 20px;
  row-gap: 48px;
}
@media (max-width: 1023px) {
  .news__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 639px) {
  .news__list {
    margin-top: 30px;
    grid-template-columns: repeat(1, 1fr);
  }
}
.news__list img {
  object-fit: cover;
}
@media (hover: hover) {
  .news__list a:hover:not(.focus-visible) .news__title,
.news__list a:hover:not(.focus-visible) .news-teaser {
    color: #3FA9F5;
  }
}
.news__list a.focus-visible:focus .news__title,
.news__list a.focus-visible:focus .news-teaser {
  color: #3FA9F5;
}

.news__thumb {
  margin-bottom: 36px;
}
@media (max-width: 1023px) {
  .news__thumb {
    margin-bottom: 25px;
  }
}
@media (max-width: 639px) {
  .news__thumb {
    margin-bottom: 18px;
  }
}

.news__title {
  font-size: 24px;
  line-height: 120%;
  color: #000000;
  margin-bottom: 18px;
  transition: color 0.3s ease;
}
@media (max-width: 1023px) {
  .news__title {
    font-size: 18px;
    margin-bottom: 8px;
  }
}
@media (max-width: 639px) {
  .news__title {
    font-size: 16px;
  }
}

.news-teaser {
  font-size: 16px;
  line-height: 150%;
  color: #717171;
  transition: color 0.3s ease;
}

.label-news {
  display: inline-block;
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  padding: 4px 10px;
  margin-bottom: 16px;
  border-radius: 6px;
  transition: all 0.3s ease;
}
.label-news--single {
  color: #000000;
  background-color: #E1DEDE;
}
@media (hover: hover) {
  .label-news--single:hover:not(.focus-visible) {
    color: #ffffff;
    background-color: #000000;
  }
}
.label-news--single.focus-visible:focus {
  color: #ffffff;
  background-color: #000000;
}
.label-news--all {
  color: #ffffff;
  background-color: #A3A3A3;
}

.resent-post {
  position: relative;
  display: flex;
  align-items: flex-end;
  padding-bottom: 60px;
  padding-left: 130px;
  padding-right: 130px;
  min-height: 550px;
  margin-bottom: 70px;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .resent-post {
    min-height: 400px;
    padding-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
  }
}
.resent-post::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 22, 36, 0.4);
  z-index: 1;
}
.resent-post img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  width: 100%;
}
@media (max-width: 1023px) {
  .resent-post img {
    height: 100%;
  }
}
.resent-post a {
  color: #ffffff;
}
@media (hover: hover) {
  .resent-post a:hover:not(.focus-visible) {
    color: #acc4f5;
  }
}
.resent-post a.focus-visible:focus {
  color: #acc4f5;
}
@media (max-width: 639px) {
  .resent-post svg {
    width: 50px;
    height: 50px;
  }
}
.resent-post h3 {
  max-width: 720px;
  font-size: 36px;
  line-height: 130%;
  margin-bottom: 0;
}
@media (max-width: 1023px) {
  .resent-post h3 {
    font-size: 30px;
    margin-bottom: 10px;
  }
}
@media (max-width: 639px) {
  .resent-post h3 {
    font-size: 25px;
  }
}

.resent-post__content {
  width: 100%;
  position: relative;
  z-index: 2;
  color: #ffffff;
}

.resent-post__date {
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
}

.reset-post__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.service-pages a {
  text-decoration: underline;
  color: #3fa9f5;
}

